.contents {
    header {
        background-color: $darkblue;
    }

    .header-block, .info {
        margin: 0 auto;
        max-width: 95%;
        width: 64rem;
    }

    .header-block {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1.5rem 0;
        &__logo {
            width: 7rem;
            height: 7rem;
        }
        &__title {
            font-size: 6rem;
            color: $white;
            margin: 0.75rem 0 0 0.75rem;
        }
    }

    .info {
        display: flex;
        .schedule {
            &__heading {
                text-align: left;
                text-transform: uppercase;
                font-size: 4rem;
            }
            &__list {
                &--item {
                    a {
                        font-size: 1.5rem;
                    }
                }
            }
        }

        .instructor {
            // position: absolute;
            // right: 0;
            // top: 0;
            &__image {
                // width: 30rem;
            }
        }
    }


}